export default {"name":"普洱茶","img":"\\heicha\\puercha\\img_201911221326531pwhd3.jpg","desc":["普洱茶，又名滇青茶}，因原运销集散地在普洱县，故名普洱茶。普洱茶以云南大叶种晒青茶为原料，使用亚发酵青茶制法，从发酵不同分为生茶和熟茶两种，成品分为散茶和紧压茶两类。普洱茶讲究冲泡技巧和品饮艺术，其饮用方法丰富，既可清饮，也可混饮。普洱茶茶汤橙黄浓厚，香气高锐持久，香型独特，滋味浓醇，经久耐泡，冲泡五六次后仍有香味。普洱茶主要产于云南省的西双版纳、临沧、普洱等地区。该地具有终年雨水充足、云雾弥漫、土层深厚、土地肥沃、无污染等优势。"],"menus":[{"name":"普洱茶茶叶历史","content":[{"text":"普洱茶历史非常悠久，早在三千多年前武王伐纣时期，云南种茶先民濮人就已经献茶给周武王，只不过那时还没有普洱茶这个名称。邦崴过渡型古茶树是古代濮人栽培驯化茶树遗留下来的活化石。"},{"text":"历史文献中记载最早种植普洱茶的人是唐吏樊绰，在其所著《蛮书》卷七中云“茶出银生城界诸山，散收无采造法。蒙舍蛮以椒姜桂和烹而饮之。”据考证银生城的茶应该是云南大叶茶种，也就是普洱茶种。历史记载说明，早在1100多年前，属南诏“银生城界诸山”的思普区境内，已盛产茶叶。宋朝李石在他的《续博物志》一书也记载了：“茶出银生诸山，采无时，杂菽姜烹而饮之。”从茶文化历史的认知，茶兴于唐朝而盛于宋朝。"},{"src":"\\heicha\\puercha\\img_201911221326533of6o9.jpg"},{"text":"元朝时有一地名叫“步日部”，由于后来写成汉字，就成了“普耳”(当时“耳”无三点水)。普洱一词首见于此。"},{"text":"明代万历年间谢肇淛在其著《滇略》中，提到“普茶”(即普洱茶)这个词，该书曰：“士庶所用，皆普茶也，蒸而成团”。这是“普茶”一词首次见诸文字。明代李时珍著《本草纲目》中亦有“普洱茶出云南普洱”的记载。"},{"text":"明朝，茶马市场在云南兴起，来往穿梭云南与西藏之间的马帮如织。在茶道的沿途上，聚集而形成许多城市。以普洱府为中心点，透过了古茶道和茶马大道极频繁的东西交通往来，进行着庞大的茶马交易。[1]"},{"text":"清朝阮福《普洱茶记》：“普洱古属银生府。则西蕃之用普洱，已自唐时。”清道光《普洱府志》“六茶山遗器”载，在1700多年前的三国时期，普洱府境内就已种茶。"},{"text":"清人檀萃在《滇海虞衡志》中有载：“普茶名重于天下，出普洱所属六茶山……入山作茶者数十万人”。此六茶山在普洱府属思茅厅界内。普洱府思茅厅是当时茶叶贸易的集散地，六大茶山的茶叶大部份集中到普洱府，经过帆工精制后，运销国内外，故称普洱茶。普洱茶是清代云南进贡朝廷的贡品，雍正年间以来，向皇帝进贡的普洱茶就是由普洱府思茅同知承办的。"},{"text":"新中国诞生后，云南茶叶获得了新生。1951年就建立了全省茶叶科研机构，到1958年止，全省茶园面积达到了46.6万亩，产茶18万担，但大跃进时期茶树大受摧残。1966年，云南茶仍只有19万担。十年动乱期间，茶叶发展缓慢。1975年普洱熟茶用于生产，随着逐年的发展人们对普洱熟茶的了解认可，工业化批量生产势在必行，可传统的熟茶工艺制作过程相对缓慢，各种香型需靠陈放几年缓慢转化。1976年，面积到了127万亩，而产量仅有32万担。1984年，现代普洱创始人吴启英通过普洱茶接种技术科学的方式，在保证普洱茶质量的情况下22天就完成了普洱熟茶的发酵转化。这是现代普洱熟茶的开端，为普洱熟茶批量生产走向世界奠定了基础。1990年，茶园面积达240万亩。"},{"text":"1998年产量达155万担，出口20多万担，创汇2250万美元，创造了云南茶史的最大辉煌，茶类由1950年的单一晒青发展到炒青\n        绿茶\n        、工夫\n        红茶\n        、ctc红碎茶、普洱茶、\n        花茶\n        、速溶茶、名特优茶、艺术品茶等。"},{"text":"2003年3月云南省标准计量局于公布普洱茶的定义：“普洱茶是以云南省一定区域内的云南大叶种晒青毛茶为原料，经过后发酵加工而成的散茶和紧压茶”。"}]},{"name":"普洱茶茶叶特点","content":[{"src":"\\heicha\\puercha\\img_20191122132653y6g86v.jpg"},{"text":"普洱茶按压制形状"},{"text":"1. 饼茶：扁平圆盘状，其中七子饼每块净重357克，每七个为一提，每筒重2500克，故名七子饼"},{"text":"2. 沱茶：形状跟饭碗一般大小，每个净重100克、250克，迷你小沱茶每个净重2克—5克"},{"text":"3. 砖茶：长方形或正方形，250克—1000克居多，制成这种形状主要是为了便于运送"},{"text":"4. 金瓜贡茶：压制成大小不等的半瓜形，从100克到数百斤均有"},{"text":"5. 香菇紧茶：压制成香菇状的普洱茶，重量约为250克"},{"text":"6. 柱茶：压制成长柱状的普洱茶，再用竹片或荀壳包扎在外面，重量小至100克，大至千克以上"},{"text":"7. 七子饼：圆形的茶饼通常七饼包装成一提，所以称为七子饼，重量小至25克，大至3公斤"},{"text":"8. 小金沱：圆形的沱茶，重量2克"},{"text":"9. 老茶头：老茶头，也叫自然沱，是晒青毛茶中比较肥嫩的芽叶，由于嫩度高，果胶含量高，在熟茶的渥堆发酵过程中，自然卷曲或纠结成块，形成了茶头。陈年茶头紧结，发酵充分，非常耐泡，为熟茶中精华，口感汤色极佳;"},{"text":"发酵工艺分类"},{"text":"1.生茶是新鲜的茶叶采摘后以自然的方式陈放，未经过渥堆发酵处理。生茶茶性较烈，刺激。新制或陈放不久的生茶有强烈的苦味，色味汤色较浅或黄绿。生茶储藏时间越久香味越醇厚。"},{"text":"2.熟茶是经过渥堆发酵使茶性趋向温和，熟普具有温和的茶性，茶水丝滑柔顺，醇香浓郁，更适合日常饮用。熟普的香味会随着陈化的时间而变得越来越柔顺，浓郁。熟普以1973年为分界点，1973年之前没有熟茶。"},{"text":"普洱茶规格分类"},{"text":"初制毛茶分为春、夏、秋三个规格。春茶又分春尖、春中、春尾三个等级;夏茶又称二水;秋茶称为谷\n        花茶\n        。普洱茶中以春尖和谷花品质最佳。"},{"text":"制法分类"},{"text":"1. 生茶：普洱茶(生茶)是以符合普洱茶产地环境条件下生长的云南大叶种茶树鲜叶为原料，经杀青、揉捻、日光干燥、蒸压成型等工艺制成的茶包括散茶及紧压茶。其品质特征为：外形色泽墨绿、香气清纯持久、滋味浓厚回甘、汤色绿黄清亮、叶底肥厚黄绿。"},{"text":"2. 熟茶：普洱茶(熟茶)是以符合普洱茶产地环境条件的云南大叶种晒青茶为原料，采用渥堆工艺，经后发酵(人为加水提温促进细菌繁殖，加速茶叶熟化去除生茶苦涩以达到入口纯和汤色红浓之独特品性)加工形成的散茶和紧压茶。其品质特征为：汤色红浓明亮，香气独特陈香，滋味醇厚回甘，叶底红褐均匀。"},{"text":"存放方式分类"},{"text":"干仓普洱：指存放于通风、干燥及清洁的仓库，使茶叶自然发酵，陈化10—20年为佳。"},{"text":"湿仓普洱：通常放置于较潮湿的地方，如地下室、地窖，以加快其发酵速度。由于茶叶内含物破坏较多，常有泥味或霉味，湿仓普洱陈化速度虽较干仓普洱快，但容易产生霉变，对人体健康不利。"},{"text":"按原料分类"},{"text":"普洱茶制作选用的原料不同其品质也不同，价格更加悬殊。其制茶原料分为“人工种植型原料”、“原生态乔木大树型原料”台地茶"},{"text":"1. 人工种植型原料。人工种植型，茶农称“台地茶”，是从人工栽培的梯地茶园里采取茶青原料，经加工制成的普洱茶。台地茶发芽期长而荫芽周期短，茶质没有适当的积累期，芽叶外表肥壮而质薄，且茶树没有合理的休养期，对茶树的生长和来年的茶质都有较大影响。台地茶由于养分不足，内质较差，只能依靠拼配提升滋味，还有耕种施肥行为，只能体现出清香，所以“台地茶”普遍价格低廉。"},{"text":"2.原生态乔木大树型原料。原生态乔木大树型原料，又称“古树茶”纯料。一般以300年为限才可严格的成为“古树茶”，且都生长在深山老林中，普洱茶树经过时间的沉淀，其内质的表现力不同。古树茶获取土壤深层的矿物质成分，能以内质丰富的最佳状态将各山头的独特性体现出来。此类原料制成的普洱茶为“饮茶发烧友”追捧，较之人工种植型原料的高产，古树茶原料有限，市面上价格较高。但其独特内质更能体现普洱茶的“茶文化”。"}]},{"name":"普洱茶加工工艺","content":[{"src":"\\heicha\\puercha\\img_201911221326534pg5u2.jpg"},{"text":"鲜叶采摘"},{"text":"普洱茶茶青的采收从每年2月下旬至11月中旬都可进行，思、普地区种植的大叶种茶树，一年可发5～6轮，生长期在300天以上。由于云南当地的气候特点。按照传统的划分方法，清明至谷雨所采的茶叶称为“春尖”，芒种至大暑所采的茶叶称为“二水”，白露至霜降所采的茶叶称为“谷花”。一般说来，“春尖”及“谷花”两个时期的茶品质最好。茶叶采摘时，叶和芽同时采，一般从茶树枝条的尖往下采摘到第三叶及同等嫩度的对夹叶：一叶一芽的采一芽、二叶一芽的采一叶一芽、三叶一芽的采二叶一芽，合理留叶。分级时，级别高的芽多，级别低的叶多梗多。手工采摘要提手采;机采要保证鲜叶质量，保证无害化，防止污染。[4]"},{"text":"分级指标"},{"text":"普洱茶级别的划分是一嫩度为基础，嫩度越高的级别也越高，衡量嫩度的高低主要看三点，一是看芽头的多少。芽头多，毫显，嫩度高，二是看条索(叶片卷紧的程度)紧结，重实的程度，紧结，重实的嫩度好;三是色泽光润的程度.色泽光润，润泽的嫩度好，色泽干枯的嫩度差。"},{"text":"特级外型条索紧直较细，显毫;内质汤色红浓，陈香浓郁，滋味醇厚，叶底较褐红细嫩。"},{"text":"一级外型条索紧结稍嫩，较显毫;内质汤色红浓，滋味醇和，香气浓纯，叶底褐红肥嫩。"},{"text":"三级外形条索紧结，尚显毫;内质汤色红浓，滋味醇和，香气浓纯，叶底褐红柔软。"},{"text":"五级外型条索坚实，略显毫;内质汤色深红，滋味醇和，香气纯正，叶底褐红欠匀，尚柔软。"},{"text":"七级外型条索肥壮，紧实，色泽褐红，稍灰，内质汤色深红，滋味醇和，香气纯和，叶底褐红欠匀，尚嫩。"},{"text":"八级外行条索肥壮，色泽褐红稍灰;内质汤色深红，滋味醇和，香气纯和，叶底褐红欠匀，尚嫩。"},{"text":"九级外型条索粗大尚紧实，色泽褐红稍灰;内质汤色深红，滋味醇和，香气纯和，叶底褐红欠匀，尚嫩。"},{"text":"十级外型条索稍松，色泽褐红稍花;内质汤色深红，滋味平和，香气平和，叶底褐红稍粗。[5]"},{"text":"原料加工"},{"text":"鲜叶摊放：鲜叶分级摊放至含水量70%左右进行杀青。"},{"text":"杀青：杀青要杀透、杀匀，无青草气味和烟气味。杀青叶及时揉捻成条。"},{"text":"揉捻：揉捻加压不宜过重，时间为30分至40分。"},{"text":"解块：解散结块茶。"},{"text":"日光干燥：必须以日光晒干，其间可再揉捻一次以使茶条紧结，晒青茶含水量≤10%。"},{"text":"成品茶加工"},{"text":"1.晒青茶精制：对晒青茶进行筛制和拼配。"},{"text":"2.筛制：通过筛分、风选、拣剔，达到分级要求。"},{"text":"3.拼配：根据拟生产的普洱茶品质要求合理拼配。"},{"text":"4.蒸压成型：把晒干的茶叶用蒸汽蒸湿，放在不同模具里压成形。提取香味及使茶叶中果胶溢出表皮，利于压制成型和有别于散茶的独特香味。蒸压前须测定每批预制茶含水率并计算确定称茶量。"},{"text":"5.干燥：干燥温度≤60℃，至茶叶含水量≤13%。"},{"text":"普洱茶(熟茶)散茶"},{"text":"1. 晒青茶后发酵：根据茶叶等级和气候条件合理确定茶水比例，对晒青茶进行后发酵。发酵过程中应适时翻堆解块，堆温控制在65℃以下。"},{"text":"2. 渥堆发酵：将湿水后的茶叶按一定厚度堆积在一起，在微生物作用、湿热作用和氧化作用的共同作用下，形成普洱茶特有的风味、品质。"},{"text":"3. 干燥：完成发酵的茶叶经自然干燥至含水量≤12%。"},{"text":"4. 精制：对发酵好的茶叶进行筛制和拼配。"},{"text":"5. 筛制：通过筛分、风选、拣剔，达到分级要求。"},{"text":"6. 拼配：根据拟生产的普洱茶品质要求合理拼配。"},{"text":"普洱茶(熟茶)紧压茶"},{"text":"1. 蒸压成型：把晒干的茶叶用蒸汽蒸湿，放在不同模具里压成形。提取香味及使茶叶中果胶溢出表皮，利于压制成型和有别于散茶的独特香味。蒸压前须测定每批预制茶含水率并计算确定称茶量。"},{"text":"2. 干燥：干燥温度≤60℃，至茶叶含水量≤12.5%。"}]},{"name":"普洱茶冲泡方法","content":[{"src":"\\heicha\\puercha\\img_20191122132653h0dhnz.jpg"},{"text":"投茶量"},{"text":"熟茶汤色，冲泡普洱茶时，投茶量的大小与饮茶习惯、冲泡方法、茶叶的个性有着密切的关系，富于变化。就云南人的饮茶习惯而言，采用留根闷泡法时，冲泡品质正常的茶叶，投茶量与水的质量比一般1：40或1：45。如果采用“功夫”泡法，投茶量可适当增加，通过控制冲泡节奏的快慢来调节茶汤的浓度。就茶性而言，投茶量的多少也有变化。例如熟茶、陈茶可适当增加，生茶、新茶适当减少等等。"},{"text":"冲泡普洱茶："},{"text":"普洱茶冲泡需先冲一次热水，称为“洗茶”。“洗茶”概念出现于明代，《茶谱》(明朝)载“凡烹茶，先以热汤洗茶叶，去其尘垢、冷气，烹之则美。”对于普洱茶，“洗茶”这一过程必不可少。因为大多数普洱茶都是隔年甚至数年后饮用的。“洗茶”时注意掌握节奏，杜绝多次“洗茶”或高温长时间“洗茶”，减少茶味流失。普洱茶冲泡宜选腹大的壶，因为普洱茶的浓度高，用腹大的壶可避免茶汤过浓，材质宜选陶壶、\n        紫砂壶\n        。而第二次以后浓淡的选择就可依照个人喜好来决定。普洱茶也可冷饮。"},{"text":"泡茶水温"},{"text":"水温的掌握对茶性的展现有着重要的作用。高温有利于发散香味，有利于茶味的快速浸出。但高温也容易冲出苦涩味，容易烫伤一部分高档茶。水温的高低因茶而异。例如，用料较粗的饼砖茶、紧茶和陈茶等适宜沸水冲泡;用料较嫩的高档芽茶(如较新的宫廷普洱)、高档青饼适宜适当降温冲泡。避免高温将细嫩茶烫熟成为“菜茶”。在冲泡部分高档新青茶时，除直接降温外，还可通过不加壶盖或沸水高冲来降低水温，避免因茶叶烫熟而产生“水闷气”。"},{"text":"冲泡时间：冲泡时间长短的控制，目的是为了让茶叶的香气、滋味展现充分准确。由于云南普洱茶的制作工艺和原料选择的特殊性，决定了冲泡的方式方法和冲泡时间的长短。陈茶、粗茶冲泡时间长，新茶、细嫩茶冲泡时间短;手工揉捻茶冲泡时间长，机械揉捻茶冲泡时间短;紧压茶冲泡时间长，散茶冲泡时间短。"},{"text":"飘逸杯冲泡法"},{"text":"一个飘逸杯分为外杯、内杯和杯盖组成。外杯是玻璃的，内杯是一个带阀门的高温塑料小杯，内杯带有滤网和阀门开关，只有打开阀门时，内杯中的茶汤才会通过滤网从内杯下方流到外杯中。"},{"text":"1. 撬茶：用茶刀从各种普洱紧压茶(饼、砖、沱等)撬下适量(5-10克)普洱茶;"},{"text":"2. 投茶：将撬下的茶叶投入内杯中;"},{"text":"3. 准备冲泡：将装有茶叶的内杯放入外杯中;"},{"text":"4. 第一泡：将沸水冲入杯中，由于阀门是关闭的，茶汤只能在内杯中浸泡茶叶，这第一泡也叫洗茶，和功夫泡法原理相同;"},{"text":"5. 打开阀门：迅速按动开关打开内杯阀门，让茶汤流到外杯中;"},{"text":"6. 洗杯：用第一泡茶汤涮洗外杯然后倒掉，有助于提高普洱茶的醇厚味道;"},{"text":"7. 第二泡：再次冲入沸水泡茶，这一泡开始可以饮用;"},{"text":"8. 出汤：按动开关打开内杯阀门，让茶汤流到外杯中，若汤量不够还可再次冲泡，多次出汤;"},{"text":"冲泡误区"},{"text":"冲泡普洱茶的水温在90℃以上，茶的香气和滋味才会更明显。但水不可反复烧开，反复烧开的水中会有大量盐类析出，析出的盐类会悬浮在水中，形成白色的浮渣。烧水过程中水不断蒸发，每烧开一次，盐类的浓度就增加一次。这些不溶解的盐类本身就口感极差，有些甚至会与茶叶成分发生反应，影响茶汤口味。有些金属元素，比如铜、铁，在多次烧开的水中浓度增加，它们与茶叶的有效成分——茶多酚发生反应，破坏香味。而且，反复烧开的水含有亚硝酸盐，进入人体后会生成致癌的亚硝酸胺。"}]},{"name":"普洱茶品饮技巧","content":[{"text":"普洱茶既可清饮，也可混饮。清饮指不加任何辅料来冲泡，多见于汉族;混饮是指于在茶中添加辅料，多见于香港、台湾，如香港在普洱茶中加入菊花、枸杞、西洋参等养生食料。"},{"text":"品饮普洱茶必须趁热闻香，举杯鼻前，此时即可感受陈味芳香如泉涌般扑鼻而来，其高雅沁心之感，不在幽兰清菊之下。普洱茶需用心品茗，啜饮入口，始能得其真韵，虽茶汤入口略感苦涩，但待茶汤于喉舌间略作停留时，即可感受茶汤穿透牙缝、沁渗齿龈，并由舌根产生甘津送回舌面，此时满口芳香，甘露“生津”，令人神清气爽，而且津液四溢，持久不散不渴，此乃品茗之最佳感受“回韵”。"}]},{"name":"普洱茶功效作用","content":[{"src":"\\heicha\\puercha\\img_201911221326532ln08i.jpg"},{"text":"药用价值："},{"text":"普洱茶的具体功效：普洱茶功效的现代研究共列述20项，即减肥、降脂、防治动脉硬化、防治冠心病、降压、抗衰老、抗癌、降糖、抑菌消炎、减轻烟毒、减轻重金属毒、抗辐射、兴奋中枢神经、利尿、防龋齿、明目、助消化、抗毒、灭菌、预防便秘、解酒及其他，部分功效是与中医有关，如兴奋中枢神经与少睡有关、利尿与排毒有关等。还能能帮助消化，有效刺激人体新陈代谢加速分解小腹的赘肉。下面主要简述茶叶中主要特质所产生的这些功效的作用和机理。"},{"text":"基本功能："},{"text":"⒈降脂、降压、抗动脉硬化。"},{"text":"⒉防癌、抗癌。"},{"text":"⒊养胃、护胃。"},{"text":"⒋健牙护齿。"},{"text":"⒌消炎、杀菌、治痢。"},{"text":"⒍抗衰老、凝神。"},{"text":"⒎美容。"},{"text":"⒏醒酒。"},{"text":"⒐利尿"},{"text":"⒑养颜"},{"text":"11.减肥"},{"text":"12 祛暑"},{"text":"功能详解："},{"text":"⒈降脂、减肥、养颜、降压、抗动脉硬化。法国巴黎圣安东尼医学院临床教学主任艾米尔·卡罗比医生用云南普洱沱茶临床试验证明：“云南普洱茶对减少类脂化含物、胆固醇含量有良好效果”。普洱茶与脂肪的代谢关系密切，普洱茶经过独特的发酵过程生成了新的化学物质，其中有的含有脂肪分解酵素的脂肪酶，能对脂肪产生分解作用，因而普洱茶有减肥的效果中国昆明医学院也对云南普洱沱茶治疗高脂血病作了55例临床试验，并与降脂效果较好的药物安妥明治疗的31例对比，普洱茶的疗效高于安妥明。长期饮用普洱茶能使胆固醇及甘油脂减少，所以长期饮用普洱茶有治疗肥胖症的功用。饮用普洱茶能引起人的血管舒张、血压下降、心率减慢和脑部血流量减少等生理效应，所以对高血压和脑动脉硬化患者有良好治疗作用。普洱茶，相对血管软化效果好，对促进肠道循环排毒效果差一些。普洱茶，相对对血管软化效果好。"},{"text":"⒉防癌、抗癌科学家通过大量的人群比较，证明饮茶人群的癌症发病率较低。而普洱茶含有多种丰富的抗癌微量元素，普洱茶杀癌细胞的作用强烈。"},{"text":"⒊健齿护齿普洱茶中含有许多生理活性成分，具有杀菌消毒的作用，因此能去除口腔异味，保护牙齿。"},{"text":"⒋护胃、养胃在适宜的浓度下，饮用平和的普洱茶对肠胃不产生刺激作用，粘稠、甘滑、醇厚的普洱茶进入人体肠胃形成的膜附着胃的表层，对胃产生有益的保护层，长期的饮用普洱茶后可以起到护胃、养胃的作用。这是国内外崇尚饮用普洱茶的消费者称谓普洱茶为“美容茶 ”、“益寿茶”的主要原因。"},{"text":"⒌抗衰老茶叶中儿茶素类化含物具有抗衰老的作用，云南大叶种茶所含儿茶素，总量高于其他茶树品种，抗衰老作用优于其他茶类。同时，普洱茶在加工过程中，大分子多糖类物质转化成了大量新的可溶性单糖和寡糖，维生素C成倍增加，这些物质对提高人体免疫系统的功能发挥着重要作用，起到了养生健体、延年益寿的功效。"},{"text":"⒍防辐射据广东中山大学何国藩等用普洱茶进行的研究结果表明，饮用2%普洱茶可以解除用钴60辐射引起的伤害。"},{"text":"⒎醒酒《本草纲目拾遗》载：“普茶最治油蒙心包，刮肠、醒酒第一。” 事实医学证明：茶叶中的茶多酚能促进乙醇代谢，对肝脏有保护作用。使乙醇代谢能正常顺利进行。喝茶能增加血管收缩功能。茶碱具有利尿作用，能促使酒精快速排出体外，减少酒醉后的危害。饮茶还可以补充酒精水解所需的维生素C，兴奋被酒精麻醉的大脑中枢。因而起到解酒作用。并且用茶解酒，绝对不会伤害脾胃，不会使醉者大量呕吐，发生反胃的痛苦。"},{"text":"⒏美容普洱茶能调节新陈代谢，促进血液循环，调节人体，自然平衡体内机能，因而有美容的效果，在海外被称为“美容茶”。"},{"text":"⒐明目青普喝后的茶渣乘热敷于眼部几分钟，长期坚持，有明目的作用。尤其是长期面对电脑显示器的网虫、白领等等，因为普洱茶有防辐射的作用，所以这一招尤其有效。"},{"text":"⒑养脚熟普喝后的茶渣集中起来煮水可泡脚，因为熟茶性温，泡脚后能促进足部血液循环，使全身经络更加通畅，而且还能防治冻疮，冬天还能很好的暖足作用。尤其是对中、老年人特别有效。"},{"text":"⒒6年以上的普洱是可以在睡前喝的安神茶，而且无论年龄大小都可以睡前饮用。"},{"text":"⒓普洱茶汁中生物碱衍生物能排除人体内的尼古丁，抑制细胞发生突变变成癌。达到清除烟毒的作用。"},{"text":"食用价值："},{"text":"普洱茶粥"},{"text":"用普洱茶和甘菊花泡茶，用其茶汤来煮粥。但普洱茶的色泽决定了这粥呈红中带黑的颜色。"},{"text":"入菜"},{"text":"普洱茶除了饮用之外，还可以用来入菜，其主要好处就是去油腻，清肠胃，因此普洱茶大都是用来烹调肉类的。另一方面，普洱茶的甘醇香气入到菜味中，也可以锦上添花。用普洱茶入菜的前提是要选用优质的普洱茶。广州的普洱菜式较港台少，在台湾，已经发展出了一系列复杂的“普洱料理”，如普洱鸡、普洱鱼、普洱贝、普洱蛋、普洱佛跳墙等等。"},{"text":"保健功效"},{"text":"云南地处祖国边疆，西双版纳地处云南边陲，山高水险.在古代交通极为艰难，茶叶的外运全靠马帮牛帮，山路上耽搁的时间很长，有的路段马帮一年只能走两趟，牛帮则一年只能走一转，茶在马背、牛背上长时间颠簸， 日晒风吹雨淋，使其内含物质徐徐转化，导致普洱茶的独特泽更明、陈香风味更浓。药效功能倍增。柴萼著于公元1925年的《梵天卢丛录》、清人赵学敏《本草纲目拾遣》《思茅采访》、《物理小识》、《百草镜》、《滇南闻见录》等古籍，都有普洱茶解毒、治病的记载。国内外对普洱茶的生理、药理功能进行了更加深入的研究，普洱茶的功能也进一步的得到了开发。新进的研究发现普洱\n        茶具\n        有美容的效果，被誉为是“美容新贵”，可以.深层排毒。普洱茶可以生津止渴，消暑，解毒，通便。特别适合有便秘困扰的MM，借助普洱茶，能够调节肠胃，恢复正常功能。彻底解决因便秘引起的痘痘以及“淑女杀手”的口臭问题;可以摆脱肤色暗沉。普洱茶最大的一个功效就是可以降低血脂含量，使血管舒张，从而加速血液循环，解决 因气血不畅引起的肤色暗沉以及各种恼人的斑点;可以纤体紧肤。"},{"text":"普洱茶的功效首先在于普洱茶茶性温和，暖胃不伤胃。这点对熟普洱茶尤为明显。一般轻发酵茶对爱茶人来说，确有“不可承受之轻”的顾忌，尤其是我们这些整日喝茶又饮食不正常的现代人，在咖啡因、单宁酸的长期浸渍下，常有搜肠刮胃之苦，相信很多人都有切身体会。所以普洱茶特别适合应酬多多的朋友。多数饮酒过度的朋友都有胃寒的毛病，普洱茶独有的暖胃功效，让家人每天饮用特别好，也可以让他感受到一份贴心关怀。"},{"text":"云南普洱茶为茶中之茶，是暖胃、降脂、养气、益寿延年及品茗的圣品。云南有最古老的茶树王，也是茶的原产地，早在明末清初至同治年间，帝王、皇族中便崇尚喝普洱茶，此时也是普洱茶的兴盛时期。普洱茶是一种后发酵茶，具有“长寿茶”的美称，亦称“可以喝的古董”，可长期存放、愈陈愈香。"},{"text":"普洱茶泡饮时，可以加入适量的牛蒡(药店晒干的约三片左右)长期饮用可以治小便便秘、牙龈肿痛等等。它的香气、滋润，滋味醇厚，海外侨泡和港澳同胞常将普洱茶当作养生妙品。"},{"text":"饮用价值："},{"text":"春季饮茶重养生"},{"text":"春季人体处于舒发之际，可选择有一定自然发酵程度的普洱茶或生熟混拼的普洱茶，这样的茶既有温性，又有活性，更利于散发冬天积郁在人体内的寒邪，能促进人体阳气生发，使人精神振奋，消除春困，增强疾病的抵御能力。"},{"text":"夏季饮茶益祛暑"},{"text":"夏季溽暑蒸人，此时宜饮生茶，其味略苦，茶性偏寒，具有消热、消暑、解毒、去火、降燥、止渴、生津、强心提神的功能，生茶富含茶多酚、咖啡碱、氨基酸、维生素等营养成分，饮之既有消暑解热之功，又具增添营养之效。"},{"text":"秋季饮茶强健体"},{"text":"秋季天气干燥，“燥气当令”，常使人口干舌燥，宜喝半生熟发酵程度的普洱茶，也可生茶和熟茶混用，取其两者功效。这样饮用茶性适中，介乎生熟茶之间，不寒不热，很适合秋天气候。"},{"text":"冬季饮茶保健康"},{"text":"冬季养生重在御寒保暖，提高抗病能力，此时宜喝熟普洱茶，其色褐红，暖意满怀且熟茶性味甘温，善蓄阳气，生热暖腹，能够强身补体，加奶、糖调饮芳香不改，从而增强人体对冬季气候的适应能力。在古籍《怨茅厅采访》中有这样的叙述：普洱茶具有“帮助消化，驱散寒冷，有解毒作用。”"},{"text":"制作工艺："},{"text":"普洱茶在制作过程中有一道特殊的工序是“渥堆”，它是形成普洱茶品质特征最关键的一步、在这一工序中微生物发挥了重要作用，整个“渥堆”过程中主要发生了以多酚类为主体的一系列复杂剧烈的生物转化反应和氧化反应，生物转化反应是以微生物分泌的胞外酶进行的酶促催化反应为主。但需要注意的是，这些微生物并不是茶叶本身所带有的，而是加了环境中的微生物自然接种到渥堆茶叶上去的。不过，可人工添加优势菌种进行渥堆处理。当代科学研究已经证实，普洱茶中的微生物主要为：黑曲霉(Aspergillus niger)、棒曲霉(Aspergillusclauatus)、灰绿曲霉(Aspergillusglaucus)、rmophilus)及酵母(Yeast)等。这些有益的微生物对人体的作用表现在普洱茶上主要有两方面："},{"text":"第一，形成普洱茶良发好的品质特征，甘滑、醇厚和陈香等特点;"},{"text":"第二，代谢有益物质于普洱茶中增强普洱茶保健功效，形成茶类中的特色茶。"},{"text":"副作用"},{"text":"普洱茶喝普洱茶好处很多，但也有一定的副作用，主要是针对一些女性朋友在特殊的时期及不同的体质，选择喝普洱茶的时候还有一些问题是需要注意的。女性朋友较不宜喝茶时期为："},{"text":"⒈生理期来临时 每个月生理期来临时，经血会消耗掉不少体内的铁质，因此女性朋友在此时更要多多补充含铁质丰富的蔬菜水果。但若此时习惯在饭饱之际喝茶的话，茶叶中高达50%的鞣酸会妨碍我们的肠粘膜对铁质的吸收，大大减低铁质的吸收程度，因而在肠道中，食糜中的铁质或补血药中的铁很容易结石产生沉淀的现象。"},{"text":"⒉怀孕期 孕妇正值怀孕期也不适合喝茶。一般浓茶中咖啡碱的浓度高达10%，会增加孕妇的尿量，增加心跳次数，加重孕妇的心脏和肾脏的负荷，更可能会导致妊娠中毒症，因此最好少喝茶为妙。"},{"text":"⒊孕妇临产期 孕妇将要临产前也不宜喝太多茶。因产前若喝太多茶的话，茶中的咖啡碱会产生兴奋作用而产生失眠，倘若孕妇在产前睡眠不够，那可能会导致分娩的时候精疲力竭，甚至还会造成难产的情况出现。"},{"text":"⒋产妇 生产后想亲自哺乳的产妇也不宜喝太多茶。因为这期间要是喝下大量的茶，则茶中含有的高浓度鞣酸会被粘膜吸收，进而影响乳腺的血液循环抑制乳汁的分泌，造成奶水分泌不足。茶中的咖啡碱可渗入乳汁并间接影响婴儿，对婴儿的身体健康不利。"},{"text":"⒌正值更年期的妇女，除了头晕和浑身乏力之外，有时还会出现心跳加快“脾气不好”睡眠品质差等现象，若再喝太多茶更会加重这些症状，所以喜欢喝茶的如若正值这个特殊阶段，最好适可而止。"}]},{"name":"普洱茶储藏方法","content":[{"text":"物理条件"},{"text":"1. 流通的空气。流通的空气中有较多的氧份，有利于茶叶中一些微生物的繁衍，因而可加速茶叶变化，但不能将普洱茶挂置在阳台上。要有适度流通的空气，但不能放于风口。另外，周围环境不宜有异味，否则茶叶会变味。因此，不可以摆放于厨房中或其他有生活异味或工业异味的环境里。"},{"text":"2. 恒定的温度。普洱茶放置的温度不可太高或太低，温度应以当地环境为主，不需刻意地人为创造温度，正常的室内温度即可，最佳是长年保持在摄氏20～30度之间，太高的温度会使茶叶加速发酵变酸。相对来说，春、夏、秋三季普洱茶的变化会比冬天变化快，无论何时，普洱茶都不可被太阳照射，在阴凉处为好。"},{"text":"3. 适度的湿度。好的普洱茶都讲究要干仓存放，干仓就是指在干爽的环境中存放，忌湿。太干燥的环境会令普洱茶的陈化变得缓慢，所以要有一定的湿气。在较为干燥的环境里，可以在存放茶叶的旁边摆放一小杯水，令空气中湿度稍微增大。但是太过潮湿的环境会导致普洱茶的快速变化，这种变化往往是“霉变”，令茶叶不可饮用。湿度应人为相应控制，年平均湿度不要高于75%，由于沿海一带温暖的海洋性气侯，湿度在梅雨季节会高于75%，所以更应注意及时开窗通风，散发水份。"},{"text":"存储方式"},{"text":"1. 必须在干仓陈化。干仓不会发霉，转化较为缓慢，但能保持普洱茶的真性。干仓指茶叶在湿度、温度适中、通风透气的环境中后发酵存放;湿仓是指茶商，把茶叶放在通风不畅、湿度较高的环境加速陈化，在市场上当老茶高价售出谋取利润。"},{"text":"2. 温度不可骤然变化。仓内温度如果过高，温差变化太突然，将会影响茶汤水性给予口感的活泼性，甚至仓内温度太高，形成闷热，会将原本的生茶，转变为熟普。"},{"text":"3. 避免杂味感染。茶叶是最会吸收杂气异味，洒一把干燥茶叶，会吸清空气中的异味，应力求贮放环境清洁无杂味。"},{"text":"4. 避免日晒风吹。在阳光下，酚类物质，叶绿素更容易氧化，从而使茶叶变色，变质。升高的温度亦会使茶叶加速发酵变酸。空气上，流通的空气有较多的氧份，有利于茶叶中微生物的繁衍，因而可加速茶叶变化，但不能将茶挂置在风口，否则茶气会被吹走、茶味吹散，饮用起来便淡然无味。"},{"text":"5. 利用竹箬包装。这种传统包装的质料和方式，有助于普洱茶于后发酵时，过滤杂味以确保清纯的功效。"},{"text":"6. 注意茶龄寿命。普洱茶年代寿命，到底是六十年，或一百年，或数百年，没有定论资料，往往只靠品茗者直觉研判其陈化的程度。如福元昌、同庆老号普洱圆茶陈化感已到了最高点，必须加以密封贮存，以免继续快速后发酵，造成茶性逐渐消失，品味衰退败坏。故宫的金瓜贡茶，陈期已一两百年，其品味是：“汤有色，但茶味陈化、淡薄。”"},{"text":"7. 湿度不可过高，也不可过于干燥。湿度过高，容易让茶叶变质、长霉;过于干燥，茶叶转化又会太慢。"},{"text":"普洱茶与\n        红茶\n        区别编辑 红茶及普洱茶是云南省所生产及出口的主要茶类，二者均属于“发酵茶”，理由为在其加工过程中，茶叶中的多酚类物经“发酵”过程而氧化。但由于二者“发酵”的方式及条件不同，选用的原料也不同，所产生的结果不同。"},{"text":"在红茶的“发酵”过程中，茶树鲜叶中的多酚类物质的氧化是依靠多酚氧化酶及过氧化物酶的酶促作用而完成。而普洱茶在此过程中多酚类的氧化主要是依靠湿热作用完成。因加工普洱茶的原料不是茶鲜叶而是“青毛茶”，而在“青毛茶”加工的“杀青”过程中，酶活性已被钝化，以致普洱茶所具有的氧化产物及品质特征与红茶截然不同。"}]},{"name":"普洱茶减肥原理","content":[{"text":"生物作用"},{"text":"无论是人工发酵，还是自然陈化发酵，微生物及其代谢产物都是形成普洱茶(这里指的是熟茶和陈年老茶)品质、构成普洱茶汤色滋味、实现普洱茶保健功效的重要组成部分。普洱茶发酵过程中产生的有益菌群，主要是黑曲霉、酵母属一类的，能够帮助消化，促进代谢，从而实现减肥的效果。还记得小时候吃多了不消化，医生有会让你吃一些酵母片，过一两个小时就消化了吧!微生物是世界上数量最为众多的动物，他们的存在是所有生物存在的基础。"},{"text":"物理作用"},{"text":"研究表明，普洱茶对离体肠段有减少收缩幅度和降低收缩频率的作用，小肠收缩频率减慢，意味着对食糜的笑话较不充分和对营养物质的吸收较少。并且普洱茶能使肠壁舒张，故每个蠕动波可以把食糜的推进距离增大，因而缩短食糜在肠内停留的时间，这样小肠就来不及吸收食物的营养了。因此，就能达到食物吃得多、营养吸收少的效果。"},{"text":"因为脂肪的消化周期很长，一般是饭后3个小时左右才能完成脂肪的消化，饭后喝普洱熟茶有利于减少脂肪的吸收，而脂肪则通过便便排出体外。这也就是西部边疆的游牧民族钟爱普洱茶的原因，他们以肉为主食，每天都摄入很多的蛋白质和脂肪，又由于当地缺少蔬菜和水果，维生素和纤维素的摄入量很少，因此只能通过喝普洱茶来帮助代谢摄入体内的多余脂肪，这些就是我们平时常说的普洱茶能刮脂。这里说的可是熟茶，饭后喝空腹千万那不要喝生茶哦，生茶因为没有经过发酵，茶多酚、咖啡碱、鞣酸之类的物质含量很好，对肠胃的刺激性太大，肠胃不好的人空腹喝饭后立即喝生茶，容易晕茶。"},{"text":"化学作用"},{"text":"在普洱茶减肥功能广为人知之前，\n        绿茶\n        、\n        乌龙茶\n        有减肥的功效就已经深入人心了，起主要作用的就是茶叶中所含的茶多酚、儿茶素、叶绿素、维生素C等物质，茶多酚能去脂解油腻，叶绿素能阻碍胆固醇的消化和吸收，维生素C能促进胆固醇的排泄，而且这些物质都是纯天然的、对身体没有任何副作用的。既然起作用的是这些宝贝物质，而绿茶、乌龙茶中也含有这些物质。"},{"text":"那凭什么普洱茶减肥会这么流行呢?难道只是炒作吗?非也非也!这得从制作普洱茶的原料说起。普洱茶的原料是云南大叶种茶，作为世界茶树的发源地，云南大叶种茶是世界上最原始的茶树品种，茶多酚、儿茶素、咖啡碱的含量要远远高于其它中小叶种茶树。"}]},{"name":"普洱茶鉴别方法","content":[{"text":"1. 观形。好的普洱应该是条索肥嫩紧结，叶形完整。如果是碎、细的茶沫或者杂质很多，一般是次品。普洱散茶以嫩度划分级别，从级外、一级到十级特级，嫩度越来越高。一般"},{"text":"2. 来说，嫩度越高品质越好。衡量嫩度的高低主要看四点：一看芽头多少，芽头多，毫显，嫩度高;"},{"text":"3. 闻味道。一块茶不管是新旧，是不应该有其他杂味的。熟茶经过渥堆，会有一种熟茶味。新的生茶，有清香和甜味。而陈茶应有种特殊的陈味，好的陈年普洱会散发出一种自然的"},{"text":"4. 樟香，荷香等香味，而不是“臭卜味”。"},{"text":"5. 试泡。开汤后看冲泡后的叶底(茶渣)，主要看柔软度、色泽、匀度。叶质柔软、肥嫩、有弹性的好，叶底硬、无弹性的品质不好;色泽褐红、均匀一致的好，色泽花杂不匀，或发黑、碳化、或腐烂如泥，叶张不开展属品质不好。"},{"text":"6. 试喝。好的普洱茶应该是入口柔顺滑润、滋味醇正、清爽平和，刺激性不强，可以毫不阻滞地从口腔流向喉咙和胃部。若出现麻口、苦涩异常的，要结合产地存疑，勐海区的会偏苦一些，思茅的会偏涩。"},{"text":"四看："},{"text":"1.清-味道要清，没有霉味。"},{"text":"2.纯-汤色要纯，如枣或红浓，亮，不能黑如漆。"},{"text":"3.正-存于正确环境中，位于干仓，不可处湿仓。"},{"text":"4.气-品其汤，则心旷神怡。"},{"text":"六不看："},{"text":"1. 不以错误的年代为标杆;"},{"text":"2. 不以伪造包装为依据;"},{"text":"3. 不以茶色深浅为借口;"},{"text":"4. 不以添加味道为导向;"},{"text":"5. 不以霉气仓别为号召;"},{"text":"6. 不以树龄叶种为考量。"}]},{"name":"普洱茶选购标准","content":[{"text":"普洱茶并非年代越久远越好，只有质量合格的产品在合适的储存条件下，在一定时间范围内茶品的品质会朝“好“的方向转化。一款茶品如果起初质量未能达标，或者是后期储存不当，那么不论放上多长时间，茶叶品质都不会好，而且普洱茶市场上一些不法商贩造假做旧的很多。造假手段主要就是以湿仓加快生茶的陈化速度，制造出所谓的“陈年老茶”。或者把茶的包装做旧，以表现出陈旧感。鉴定陈年普洱茶的真假和优劣鉴定对普通消费者来说有极大的困难。"},{"text":"茶叶外观"},{"text":"新普洱茶外观颜色较新鲜，多为黄绿色，带有白毫，且味道浓烈;陈茶由于经过长时间的氧化作用后，茶叶外观会呈枣红色，白毫也转成黄褐色。"},{"text":"包装纸颜色"},{"text":"通常陈年普洱茶，其包装的白纸已随时间变得陈旧，纸质略黄，因此可以从纸质手工布纹及印色的老化程度判断普洱茶的年份。但这只能作为参考，非绝对依据，因为可能有些不法商人会利用这种心理，以陈黄的包装纸包装次级品。"},{"text":"茶品年份"},{"text":"通常将普洱茶的年份划分："},{"text":"1949年以前，这时期生产的普洱茶称为“古董茶”，如百年宋聘号、百年同兴贡品、百年同庆号、同昌老号等。通常在茶饼内放有一张糯米所做、印有如上名称的纸，称为“内飞”。"},{"text":"1949—1967年，生产的茶叶在包装纸的茶字上，用不同颜色标示，红印为第一批，绿印为第二批，黄印为第三批。"},{"text":"1968年以后，这时生产的茶饼包装不再印上中国茶业公司字号，改由各茶厂自选生产，统称“云南七子拼”，包括：雪印青饼、73青饼、大中小绿印、小黄印等。"}]}]}